.section-wrapper {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.sign-form {
    display: block;
    max-width: 384px;
}

.form-group {
    position: relative;
}

.form-label, 
.form-control {
    display: block;
    width: 100%;
}

.form-label {
    padding: 0 0 0 13px;
    margin-bottom: 8px;
    letter-spacing: 1px;
}

.form-control {
    border-radius: 6px;
    text-indent: 13px;
    background-color: var(--standard-color);
}

.form-center {
    text-align: center;
}

.form-button {
    display: block;
    font-weight: bold;
    color: var(--standard-color);
    text-transform: uppercase;
    background-color: var(--orange-color);
    cursor: pointer;
}

.form-button:hover {
    -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
            transform: scale(1.05);
}

.under-button-text {
    font-size: var(--small-font-size);
}

.uppercase {
    text-transform: uppercase;
    color: var(--orange-color);
    cursor: pointer;
}

.socials-wrapper {
    max-width: 200px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.social-link {
    background-color: var(--standard-color);
    -webkit-mask-size: 100% 100%;
            mask-size: 100% 100%;
    -webkit-mask-repeat: no-repeat;
            mask-repeat: no-repeat;
    display: inline-block;
    color: transparent;
}

.social-link:hover {
    background-color: var(--orange-color);
}

.facebook {
    -webkit-mask-image: url('../assets/images/facebook-logo.svg');
    mask-image: url('../assets/images/facebook-logo.svg');
}

.instagram {
    -webkit-mask-image: url('../assets/images/instagram-logo.svg');
    mask-image: url('../assets/images/instagram-logo.svg');
}

.pinterest {
    -webkit-mask-image: url('../assets/images/pinterest-logo.svg');
    mask-image: url('../assets/images/pinterest-logo.svg');
}

.logo {
    -webkit-mask-image: url('../assets/images/myLogo.svg');
    mask-image: url('../assets/images/myLogo.svg');
}

.form-icon {
    position: absolute;
    z-index: 10;
    right: 3%;
    top: 54%;
}

.visible {
    visibility: visible;
}

.no-visible {
    visibility: hidden;
}

.info-window {
    position: absolute; 
    right: 0;
    color: var(--red-color);
}





